import App from './App'
import Auth from './Auth'
import Toast from './Toast'
import Router, { gotoRoute } from './Router'


class FamilyAPI{
  async getFamily(familyId) {
    // validate
    if (!familyId) return

    try {
      // fetch the family data from the backend
      const response = await fetch(`${App.apiBase}/family/${familyId}`, {
        headers: { Authorization: `Bearer ${localStorage.accessToken}` },
      })

      // handle non-OK response
      if (!response.ok) {
        console.log("non ok repsonse")
        const err = await response.json()
        if (err) console.log(err)
        throw new Error('Problem getting family')
      }

      // return parsed response data
      const data = await response.json()
      console.log(data)
      return data
    } catch (err) {
      console.error('Error fetching family:', err)
      Toast.show('Failed to load family data')
    }
  }

  async addMember(formData, fail = false) {
    console.log("Adding family member")

    const dataObj = Object.fromEntries(formData.entries())

    console.log(dataObj)


    const response = await fetch(`${App.apiBase}/user`, {
      method: 'POST', 
      headers: {
        'Content-Type': 'application/json'
      },       
      body: JSON.stringify(dataObj)
    })

        // if response not ok
        if(!response.ok){      
          // console log error
          const err = await response.json()
          if(err) console.log(err)
          // show error      
          Toast.show(`Problem getting user: ${response.status}`)   
          // run fail() functon if set
          if(typeof fail == 'function') fail()
          return
        }
    
        /// sign up success - show toast and redirect to sign in page
        Toast.show('Account created') 
        
        gotoRoute('/family')
  }


}

export default new FamilyAPI()