import { LitElement, html, css } from '@polymer/lit-element'
import { gotoRoute } from '../Router'
import Auth from '../Auth'
import App from '../App'
import Toast from '../Toast'



customElements.define('re-add-task', class AddTask extends LitElement {
    constructor() {
        super()
    }

    static get properties() {
        return {}
    }


  async handleSubmit(e) {
    e.preventDefault()
    const form = e.target
    const formData = new FormData(form)

    console.log(Auth.currentUser.id )
    console.log(Auth.currentUser.family_id)

    const rawOwnerId = formData.get('owner_id')?.trim()

    const taskData = {
      taskTitle: formData.get('taskTitle'),
      reward: formData.get('reward'),
      description: formData.get('description'),
      creator_id: Auth.currentUser.id,
      family_id: Auth.currentUser.family_id,
      status: rawOwnerId ? 'Assigned' : 'Pending' 
    }

    // Adding ownerid if included
    if (rawOwnerId) {
        taskData.owner_id = rawOwnerId
    }

    try {
      const response = await fetch(`${App.apiBase}/task`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${localStorage.accessToken}`
        },
        body: JSON.stringify(taskData)
      })

      if (!response.ok) throw new Error('Task creation failed')

      const createdTask = await response.json()
      Toast.show('Task created!')
      form.reset()
      gotoRoute('/tasks')

    } catch (err) {
      console.error(err)
      Toast.show('Error creating task', 'error')
    }
  }

  render() {
    return html`
    <style>
      .form-container {
        max-width: 600px;
        margin: auto;
        padding: 2rem;
        background: white;
        border-radius: 10px;
        box-shadow: 0 0 10px rgba(0,0,0,0.1);
      }

      h2 {
        text-align: center;
        margin-bottom: 1rem;
      }

      sl-input, sl-textarea, sl-select {
        margin-bottom: 1rem;
        width: 100%;
      }

      sl-button {
        width: 100%;
      }
    </style>
      <div class="page-content">
        <div class="form-container">
          <h2>Create a New Task</h2>
          <form @submit=${this.handleSubmit}>
            <sl-input name="taskTitle" placeholder="Task Title" required></sl-input>
            <sl-input name="reward" placeholder="Reward (e.g., 5 Coins)" required></sl-input>
            <sl-textarea name="description" placeholder="Task Description" required></sl-textarea>
            <sl-input name="owner_id" placeholder="Assignee User ID (Child)"></sl-input>
            <sl-button type="submit" variant="primary">Create Task</sl-button>
          </form>
        </div>
      </div>
    `
  }
})


