import App from './../../App'
import {html, render } from 'lit-html'
import {anchorRoute, gotoRoute} from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'

class SignInView {
  init(){
    document.title = 'Sign In - Rewards-Explorer'
    document.body.classList.add('auth-bg')
    this.render()
    Utils.pageIntroAnim()
  }

  signInSubmitHandler(e){
    e.preventDefault()
    const form = e.target
    const formData = new FormData(form)

    const submitBtn = form.querySelector('.submit-btn')
    submitBtn.setAttribute('loading', '')    

    // sign in using Auth    
    Auth.signIn(formData, () => {
    submitBtn.removeAttribute('loading')
    })
  }

  render(){    
  const template = html`    
      <div class="page-content page-centered" >
        <div class="signinup-box">
          <img class="signinup-logo" src="/images/logo.svg">        
          <form class="form-signup" @submit=${this.signInSubmitHandler}>          
            <div class="input-group">
              <sl-input name="email" type="email" placeholder="Email"  required></sl-input>
            </div>
            <div class="input-group">
              <sl-input name="password" type="password" placeholder="Password" required password-toggle></sl-input>
            </div>
            <sl-button class="submit-btn" type="submit" variant="primary" style="width: 100%;">Sign In</sl-button>
          </form>
          <p>No Account? <a href="/signup" @click=${anchorRoute}>Sign Up</a></p>
        </div>
      </div>
  `
  render(template, App.rootEl)    
  }
}

export default new SignInView()