// import views
import homeView from './views/pages/home'
import fourOFourView from './views/pages/404'
import signinView from './views/pages/signin'
import signupView from './views/pages/signup'
import profileView from './views/pages/profile'
import editProfileView from './views/pages/editProfile'
import guideView from './views/pages/guide'
import familyView from './views/pages/family'
import addMemberView from './views/pages/addMember'
import manageTasksView from './views/pages/manageTasks'
import editTaskView from './views/pages/editTask'

// define routes
const routes = {
	'/': homeView,	
	'404' : fourOFourView,
	'/signin': signinView,
	'/signup': signupView,
	'/profile': profileView,
	'/editProfile': editProfileView	,
	'/guide' : guideView,
	'/family' : familyView,
	'/addMember' : addMemberView,
	'/tasks' : manageTasksView,
	'/task' : editTaskView
}

class Router {
	constructor(){
		this.routes = routes
	}
	
	init(){
		// initial call
		this.route(window.location.pathname)

		// on back/forward
		window.addEventListener('popstate', () => {
			this.route(window.location.pathname)
		})
	}
	
	route(fullPathname){
		// extract path without query params
		const pathname = fullPathname.split('?')[0]

		// direct match
		if (this.routes[pathname]) {
			this.routes[pathname].init()
			return
		}

		// handling profile routes
		if (pathname.startsWith('/profile/')) {
			this.routes['/profile'].init()
			return
		}

		// handle dynamic route: /task/:id
		if (pathname.startsWith('/task/')) {
			this.routes['/task'].init()
			return
		}

		// fallback to 404
		this.routes['404'].init()
	}

	gotoRoute(pathname){
		window.history.pushState({}, pathname, window.location.origin + pathname);
		this.route(pathname)
	}	
}

// create appRouter instance and export
const AppRouter = new Router()
export default AppRouter


// programmatically load any route
export function gotoRoute(pathname){
	AppRouter.gotoRoute(pathname)
}


// allows anchor <a> links to load routes
export function anchorRoute(e){
	e.preventDefault()	
	const pathname = e.target.closest('a').pathname
	AppRouter.gotoRoute(pathname)
}
