import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute} from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import moment from 'moment'
import UserAPI from '../../UserAPI'

class ProfileView {
  async init(){
    console.log('ProfileView.init')
    document.title = 'Reward Explorers - My Profile' 
    
    this.profileUserId = window.location.pathname.split('/').pop()

    try{
      this.profileUser = await UserAPI.getUser(this.profileUserId)
      this.render()    
      Utils.pageIntroAnim()
    }

    catch {
            console.error('Failed to load user profile:', err)
            // Optionally render an error message or redirect

    }


  }

  render(){
    const template = html`
      <va-app-header title="Profile" user="${JSON.stringify(Auth.currentUser)}"></va-app-header>
      <div class="page-content"> 
        <div class="page-inner-content">

          <h1 class="page-title">View Profile</h1>
          <div class="profile-container">
                ${Auth.currentUser && Auth.currentUser.avatar ? html`
                <sl-avatar style="--size: 200px; margin-bottom: 1em;" image=${(Auth.currentUser && Auth.currentUser.avatar) ? `${App.apiBase}/images/${Auth.currentUser.avatar}` : ''}></sl-avatar>
              `:html`
              <sl-avatar style="--size: 200px; margin-bottom: 1em;"></sl-avatar>
              `}
              <h2>${this.profileUser.firstName} ${this.profileUser.lastName}</h2>
              <p>${this.profileUser.email}</p>
              
              <p>Updated: ${moment(Auth.currentUser.updatedAt).format('MMMM Do YYYY, @ h:mm a')}</p>

              <sl-button @click=${()=> gotoRoute('/editProfile')}>Edit Profile</sl-button>
          </div>
        </div>
      </div>      
    `
    render(template, App.rootEl)
  }
}


export default new ProfileView()