import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute } from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import FamilyAPI from '../../familyAPI'

class AddMemberView{
    async init() {
        console.log('AddMemberView.init')
        console.log(Auth.currentUser)
        document.title = 'Reward Explorers - Add Family Member'

        try {
        console.log(Auth.currentUser.family_id)
        this.family = await FamilyAPI.getFamily(Auth.currentUser.family_id)
        this.render()
        Utils.pageIntroAnim()
        } catch (err) {
        console.error('Error loading family:', err)
        }
    }

    signUpSubmitHandler(e){
        e.preventDefault()  
        const form = e.target
        const formData = new FormData(form)
        
        
        const submitBtn = document.querySelector('.submit-btn')
        submitBtn.setAttribute('loading', '')    


        // sign up using Auth
        FamilyAPI.addMember(formData, () => {
        submitBtn.removeAttribute('loading')
        })  
    }


  render(){
    const template = html`
        <va-app-header title="Family" user=${JSON.stringify(Auth.currentUser)}></va-app-header>

        <div class="page-content">
            <div class="page-inner-content">
                <h1 class="page-title">Add family member</h1>

                <div class="family-container green-bg">
                    <form @submit=${this.signUpSubmitHandler}>
                        <div class="input-group">
                            <sl-radio-group label="Account Type" name="accessLevel" value="1">
                                <sl-radio-button value="1">Parent</sl-radio-button>
                                <sl-radio-button value="2">Child</sl-radio-button>
                            </sl-radio-group>
                        </div>


                        <div class="input-group">
                            <sl-input name="firstName" type="text" placeholder="First Name" autocomplete="off" required></sl-input>
                        </div>
                        <div class="input-group">
                            <sl-input name="lastName" type="text" placeholder="Last Name" autocomplete="off" required></sl-input>
                        </div>
                        <div class="input-group">
                            <sl-input name="email" type="email" placeholder="Email" autocomplete="off" required></sl-input>
                        </div>
                        <div class="input-group">
                            <sl-input name="password" type="password" placeholder="Password" autocomplete="off" required password-toggle></sl-input>
                        </div>
                        <div class="input-group">
                            <input name="family_id" type="hidden" value="${Auth.currentUser.family_id}" ></input>
                        </div>



                        <div class="family-container-bottom">
                            <sl-button variant="warning" type="submit" class="submit-btn">Add member</sl-button>
                        </div>

                    </form>
                </div>
            </div>

        </div>


        


     
    `
    render(template, App.rootEl)
  }
}

export default new AddMemberView()