import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute } from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import TaskAPI from '../../taskAPI'





class ManageTasksView {
    async init() {
        console.log(Auth.currentUser)
        console.log("ManageTasks.init")
        document.title = 'Reward Explorers - Tasks'

        try {
            console.log(Auth.currentUser.family_id)
            this.tasks = await TaskAPI.getTasks(Auth.currentUser.family_id)  
            console.log(this.tasks) 

            this.render()
            Utils.pageIntroAnim()
        } catch (err) {
            console.error('Error loading family:', err)
        }
    }

    async acceptTask(taskId) {
        try {
            const updatedTask = {
            owner_id: Auth.currentUser.id,
            status: 'Assigned'
            }

            await TaskAPI.updateTask(taskId, updatedTask)

            // Refresh the tasks
            this.tasks = await TaskAPI.getTasks(Auth.currentUser.family_id)
            this.render()
        } catch (err) {
            console.error('Error accepting task:', err)
        }
    }

    async startTask(taskId) {
        try {
            const updatedTask = {
            status: 'In_Progress',
            inProgressDate: Date.now()

            }

            await TaskAPI.updateTask(taskId, updatedTask)

            // Refresh the tasks
            this.tasks = await TaskAPI.getTasks(Auth.currentUser.family_id)
            this.render()
        } catch (err) {
            console.error('Error accepting task:', err)
        }
    }

    async completeTask(taskId) {        
        try {
            const updatedTask = {
            status: 'Completed',
            completedDate: Date.now()            
            }

            await TaskAPI.updateTask(taskId, updatedTask)

            // Refresh the tasks
            this.tasks = await TaskAPI.getTasks(Auth.currentUser.family_id)
            this.render()
        } catch (err) {
            console.error('Error accepting task:', err)
        }
    }


    render(){
        // Separate tasks by category
        const availableTasks = this.tasks?.filter(task => task.status === 'Pending') || []
        const assignedTasks = this.tasks?.filter(task => task.status === 'Assigned') || []
        const inProgressTasks = this.tasks?.filter(task => task.status === 'In_Progress') || []
        const completedTasks = this.tasks?.filter(task => task.status === 'Completed') || []

        console.log(availableTasks)
        console.log(assignedTasks)
        console.log(inProgressTasks)
        console.log(completedTasks)


        const taskCard = task => html`
            <sl-card style="margin-bottom: 1rem;">
                <div slot="header"><h3>${task.taskTitle}</h3></div>
                <p>Description: <br> ${task.description}</p>
                <p>Reward: <br> ${task.reward}</p>

                
                <div slot="footer">
                <sl-badge variant="primary">${task.status}</sl-badge>

                ${Auth.currentUser.accessLevel === 1
                    ? html`
                        <a href="/task/${task._id}" @click=${anchorRoute}>Edit Task</a>
                    `
                    : ''}

                ${Auth.currentUser.accessLevel === 2 && task.status === 'Pending' && !task.owner_id
                    ? html`
                        <sl-button
                        variant="success"
                        size="small"
                        @click=${() => this.acceptTask(task._id)}
                        >
                        Accept Task
                        </sl-button>
                    `
                    : ''}

                ${Auth.currentUser.accessLevel === 2 &&
                task.status === 'Assigned' &&
                String(Auth.currentUser.id) === String(task.owner_id?._id || task.owner_id)
                    ? html`
                        <sl-button
                        variant="warning"
                        size="small"
                        @click=${() => this.startTask(task._id)}
                        >
                        Start Task
                        </sl-button>
                    `
                    : ''}
                ${Auth.currentUser.accessLevel === 2 &&
                task.status === 'In_Progress' &&
                String(Auth.currentUser.id) === String(task.owner_id?._id || task.owner_id)
                    ? html`
                        <sl-button
                        variant="success"
                        size="small"
                        @click=${() => this.completeTask(task._id)}
                        >
                        Complete Task
                        </sl-button>
                    `
                    : ''}
                </div>
            </sl-card>
        `

        const template = html`
            <va-app-header title="Home" user=${JSON.stringify(Auth.currentUser)}></va-app-header>

            <div class="page-content">
            <div class="page-inner-content">
                <h1 class="page-title">Manage Tasks</h1>




                ${Auth.currentUser.accessLevel === 1
                    ? html `
                                        <sl-details summary="Add Task">
                                                                <re-add-task></re-add-task>     
                </sl-details>
                   
                    ` : ''
                }




                <sl-tab-group>
                    <sl-tab slot="nav" panel="available">Available</sl-tab>
                    <sl-tab slot="nav" panel="assigned">Assigned</sl-tab>
                    <sl-tab slot="nav" panel="in-progress">In Progress</sl-tab>
                    <sl-tab slot="nav" panel="completed">Completed</sl-tab>


                    <sl-tab-panel name="available">
                    ${availableTasks.length > 0
                        ? availableTasks.map(taskCard)
                        : html`<p>No available tasks.</p>`}
                    </sl-tab-panel>

                    <sl-tab-panel name="assigned">
                    ${assignedTasks.length > 0
                        ? assignedTasks.map(taskCard)
                        : html`<p>No assigned tasks.</p>`}
                    </sl-tab-panel>

                    <sl-tab-panel name="in-progress">
                    ${inProgressTasks.length > 0
                        ? inProgressTasks.map(taskCard)
                        : html`<p>No tasks in progress.</p>`}
                    </sl-tab-panel>

                    <sl-tab-panel name="completed">
                    ${completedTasks.length > 0
                        ? completedTasks.map(taskCard)
                        : html`<p>No completed tasks.</p>`}
                    </sl-tab-panel>

                </sl-tab-group>  
            </div></div>
        `
        render(template, App.rootEl)
    }
}

export default new ManageTasksView()