import App from '../../App'
import { html, render } from 'lit-html'
import { gotoRoute } from '../../Router'
import Auth from '../../Auth'
import Utils from '../../Utils'
import TaskAPI from '../../taskAPI'
import moment from 'moment'


class EditTaskView {
  async init() {
    document.title = 'View Task - Rewards Explorer'
    this.taskId = window.location.pathname.split('/').pop()

    try {
      this.task = await TaskAPI.getTask(this.taskId)
      console.log(this.task)
      this.render()
      Utils.pageIntroAnim()
    } catch (err) {
      console.error('Failed to load task:', err)
    }
  }

  async handleSubmit(e) {
    e.preventDefault()
    const formData = new FormData(e.target)

    const updatedTask = {
      taskTitle: formData.get('taskTitle'),
      reward: formData.get('reward'),
      description: formData.get('description'),
      status: formData.get('status'),
      owner_id: formData.get('owner_id') || null
    }

    try {
      await TaskAPI.updateTask(this.taskId, updatedTask)
      gotoRoute('/tasks')
    } catch (err) {
      console.error('Error updating task:', err)
    }
  }

  render() {
    const isEditable = Auth.currentUser.accessLevel === 1

    const template = html`
      <va-app-header title="View Task" user=${JSON.stringify(Auth.currentUser)}></va-app-header>

      <div class="page-content">
      <div class="page-inner-content">

        <h2>${isEditable ? 'Edit Task' : 'Task Details'}</h2>

        <form @submit=${e => this.handleSubmit(e)}>

          <sl-input name="taskTitle" label="Title" value=${this.task.taskTitle} ?disabled=${!isEditable} required></sl-input>
          <sl-textarea name="description" label="Description" ?disabled=${!isEditable} value=${this.task.description || ''}></sl-textarea>
          <sl-input name="reward" label="Reward" value=${this.task.reward} ?disabled=${!isEditable}></sl-input>
          <sl-select name="status" label="Status" value=${this.task.status} ?disabled=${!isEditable}>
            <sl-option value="Pending">Pending</sl-option>
            <sl-option value="In_Progress">In Progress</sl-option>
            <sl-option value="Completed">Completed</sl-option>
          </sl-select>
          <sl-input name="owner_id" label="Owner ID" value=${this.task.owner_id || ''} ?disabled=${!isEditable}></sl-input>
          <sl-input
            name="createdDate"
            label="Created Date"
            value=${this.task.createdDate
              ? moment(this.task.createdDate).format('DD/MM/YYYY @ h:mm a')
              : 'Date not available'}
            disabled>
          </sl-input>

          <sl-input
            name="inProgressDate"
            label="Started Date"
            value=${this.task.inProgressDate
              ? moment(this.task.inProgressDate).format('DD/MM/YYYY @ h:mm a')
              : 'Task not started'}
            disabled>
          </sl-input>

          <sl-input
            name="completedDate"
            label="Completed Date"
            value=${this.task.completedDate
              ? moment(this.task.completedDate).format('DD/MM/YYYY @ h:mm a')
              : 'Task not completed'}
            disabled>
          </sl-input>



          ${isEditable
            ? html`<sl-button type="submit" variant="primary">Save Changes</sl-button>`
            : ''}
        </form>
      </div></div>
    `
    render(template, App.rootEl)
  }
}

export default new EditTaskView()