import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute } from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import FamilyAPI from '../../familyAPI'
import TaskAPI from '../../taskAPI'
import moment from 'moment'


class HomeView {
  async init(){    
    document.title = 'Home - Reward Explorers'  
    document.body.classList.remove('auth-bg') 
    this.family = await FamilyAPI.getFamily(Auth.currentUser.family_id)
    this.tasks = await TaskAPI.getTasks(Auth.currentUser.family_id)  

    this.render()    
    Utils.pageIntroAnim()    
  }



  render(){
    const taskCard = (task, showDateType) => html`
      <sl-card style="margin-bottom: 1rem;">
        <div slot="header">
          <h3>${task.taskTitle}</h3>
        </div>
        <p>Created: ${moment(task.createdDate).format('DD/MM/YY @ h:mm a')}</p>
        ${showDateType === 'started' && task.inProgressDate ? html`
          <p>Started: ${moment(task.inProgressDate).format('DD/MM/YY @ h:mm a')}</p>
        ` : ''}
        ${showDateType === 'completed' && task.completedDate ? html`
          <p>Completed: ${moment(task.completedDate).format('DD/MM/YY @ h:mm a')}</p>
        ` : ''}
        <div class="avatars" style="display: flex; gap: 0.5rem;">
          <sl-tooltip content="${task.creator_id.firstName} ${task.creator_id.lastName}">
            <sl-avatar label="Creator avatar"></sl-avatar>                
          </sl-tooltip>

          <sl-tooltip content="${task.owner_id.firstName} ${task.owner_id.lastName}">
            <sl-avatar label="Owner avatar"></sl-avatar>                
          </sl-tooltip>
        </div>
      </sl-card>
    `

    const assignedTaskCard = task => html`
      <sl-card style="margin-bottom: 1rem;">
        <div slot="header">
          <h3>${task.taskTitle}</h3>
        </div>
        <p>Created: ${moment(task.createdDate).format('DD/MM/YY @ h:mm a')}</p>
        <div class="avatars" style="display: flex; gap: 0.5rem;">
          <sl-tooltip content="${task.creator_id.firstName} ${task.creator_id.lastName}">
            <sl-avatar label="Creator avatar"></sl-avatar>                
          </sl-tooltip>

          <sl-tooltip content="${task.owner_id.firstName} ${task.owner_id.lastName}">
            <sl-avatar label="Owner avatar"></sl-avatar>                
          </sl-tooltip>
        </div>
      </sl-card>
  `


    const inProgressTasks = this.tasks.filter(t => t.status === 'In_Progress')
    const completedTasks = this.tasks
      .filter(t => t.status === 'Completed' && t.completedDate)
      .sort((a, b) => new Date(b.completedDate) - new Date(a.completedDate))
      .slice(0, 5)
    const assignedTasks = this.tasks.filter(t => t.status === 'Assigned' && t.owner_id === Auth.currentUser.id)

    const template = html`
      <va-app-header title="Home" user=${JSON.stringify(Auth.currentUser)}></va-app-header>
      
      <div class="page-content">
      <div class="page-inner-content">

        <h1 class="anim-in page-title">Welcome back ${Auth.currentUser.firstName}!</h1>

        <div class="home-grid">
          <div class="home-section family">
            <h2><sl-icon name="house"></sl-icon>${this.family.familyName}</h2>

            <div class="section-middle">
              <h3>Members</h3>

              <div class="member-list">
                ${this.family?.members.map(
                  member => html`
                    <sl-tooltip content="${member.firstName } ${member.lastName }">
                      <sl-avatar label="User avatar"></sl-avatar>                  
                    </sl-tooltip>
                  `
                )}
              </div>
            </div>

            <div class="section-bottom">
                          <sl-button variant="success" outline  @click=${() => gotoRoute('/family')}>View Family</sl-button>
            </div>

          </div>


          <div class="home-section task-roundup">
            <h2><sl-icon name="calendar-week"></sl-icon>Weekly Task Roundup</h2>


            <div class="section-middle">
                ${Auth.currentUser.accessLevel === 1
                  ? html `
                                  <h3>Created: </h3>                  
                  `
                  : ''}
                <h3>Completed: </h3>
                <h3>Assigned: </h3>
                <h3>In Progress: </h3>
            </div>

            <div class="section-bottom">
              <sl-button variant="warning" outline  @click=${() => gotoRoute('/tasks')}>View Tasks</sl-button>     
            </div>

          </div>

          <div class="home-section task">
            <h2><sl-icon name="hourglass-split"></sl-icon>In Progress</h2>
            ${inProgressTasks.map(task => taskCard(task, 'started'))}
          
          </div>
          


          <div class="home-section task">
            ${Auth.currentUser.accessLevel === 1
              ? html`<h2><sl-icon name="check2-square"></sl-icon>Recently Completed</h2>
                  ${completedTasks.map(task => taskCard(task, 'completed'))}
                `
              : html`<h2><sl-icon name="clipboard-plus"></sl-icon>Assigned Tasks</h2>
                  ${assignedTasks.map(task => assignedTaskCard(task))}
                `
            }
            

          </div>


        </div>

 
        
      </div></div>
     
    `
    render(template, App.rootEl)
  }
}

export default new HomeView()