import App from './../../App'
import {html, render } from 'lit-html'
import {gotoRoute, anchorRoute } from './../../Router'
import Auth from './../../Auth'
import Utils from './../../Utils'
import FamilyAPI from '../../familyAPI'

class FamilyView {
  async init() {
      console.log('FamilyView.init')
      document.title = 'Reward Explorers - My Family'

      // Load family data using the user's familyID
      try {
      console.log(Auth.currentUser.family_id)
      this.family = await FamilyAPI.getFamily(Auth.currentUser.family_id)
      this.render()
      Utils.pageIntroAnim()
      } catch (err) {
      console.error('Error loading family:', err)
      }
  }

  render(){
    const template = html`
        <va-app-header title="Home" user=${JSON.stringify(Auth.currentUser)}></va-app-header>

        <div class="page-content">
          <div class="page-inner-content">
            <h1 class="page-title">${this.family.familyName}</h1>

            <div class="family-container">
              <h3>Family Members</h3>
              <div class="member-container">
                ${this.family?.members.map(
                  member => html`
                    <sl-card>
                      <img slot="image"
                        src=""
                        alt="profile image" />
                      ${member.firstName} ${member.lastName}
                      <br>
                      ${member.email}
                      <div slot="footer">
                      ${Auth.currentUser.accessLevel === 1
                        ? html`<sl-button variant="danger" outline @click=${() => this.deleteMember(member._id)}>Delete</button>`
                        : ''}

                        <sl-button variant="warning" @click=${() => gotoRoute(`/profile/${member._id}`)}>View Profile</sl-button>
                      </div>
                    </sl-card>
                  `
                )}
                </div>

                <div class="family-container-bottom">

                              ${Auth.currentUser.accessLevel === 1
              ? html `<sl-button variant="success" outline @click="${() => gotoRoute("/addMember")}">Add new family member</sl-button>
              ` : ''}

                </div>


            </div>
          </div> 
        </div>      
    `
    render(template, App.rootEl)
  }
}

export default new FamilyView()